"use client";

import Link from 'next/link';
import { motion } from 'framer-motion';

export default function CTA() {
  return (
    <section id="contact" className="section bg-background">
      <div className="container">
        <motion.div 
          initial={{ opacity: 0, y: 50 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, amount: 0.3 }}
          transition={{ duration: 0.8, ease: "easeOut" }}
          className="max-w-4xl mx-auto bg-primary rounded-2xl p-8 md:p-12 text-center relative overflow-hidden"
          style={{ 
            position: 'relative',
            zIndex: 1 
          }}
        >
          {/* Background pattern */}
          <div className="absolute inset-0 z-0 opacity-20">
            <div className="absolute -right-10 -top-10 w-64 h-64 rounded-full bg-white" />
            <div className="absolute -left-10 -bottom-10 w-64 h-64 rounded-full bg-white" />
          </div>
          
          <div className="relative z-10">
            <motion.h2 
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8, ease: "easeOut", delay: 0.2 }}
              className="text-3xl md:text-4xl font-bold mb-6 text-white"
            >
              Ready to Transform Your Business?
            </motion.h2>
            
            <motion.p 
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8, ease: "easeOut", delay: 0.4 }}
              className="text-white/90 mb-8 max-w-xl mx-auto text-lg"
            >
              Contact us today to schedule a free consultation and discover how FNXLabs can help you achieve your technology goals.
            </motion.p>
            
            <motion.div 
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8, ease: "easeOut", delay: 0.6 }}
              className="flex flex-col sm:flex-row gap-4 justify-center"
            >
              <Link 
                href="mailto:info@fnxlabs.com" 
                className="bg-white text-primary hover:bg-white/90 transition-colors px-6 py-3 rounded-lg font-medium"
              >
                Contact Us
              </Link>
              <Link 
                href="tel:+15551234567" 
                className="border border-white/30 text-white hover:bg-white/10 transition-colors px-6 py-3 rounded-lg font-medium"
              >
                Call Now
              </Link>
            </motion.div>
          </div>
        </motion.div>
        
        {/* Contact form */}
        <motion.div 
          initial={{ opacity: 0, y: 50 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, amount: 0.2 }}
          transition={{ duration: 0.8, ease: "easeOut", delay: 0.2 }}
          className="max-w-3xl mx-auto mt-16"
          style={{ 
            position: 'relative',
            zIndex: 1 
          }}
        >
          <motion.div 
            initial={{ opacity: 0, scale: 0.98 }}
            whileInView={{ opacity: 1, scale: 1 }}
            viewport={{ once: true }}
            transition={{ duration: 0.8, ease: "easeOut" }}
            className="bg-accent p-8 rounded-xl"
            style={{ 
              position: 'relative',
              zIndex: 1 
            }}
          >
            <h3 className="text-2xl font-bold mb-6 text-center">Get in Touch</h3>
            
            <form className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <motion.div
                  initial={{ opacity: 0, x: -20 }}
                  whileInView={{ opacity: 1, x: 0 }}
                  viewport={{ once: true }}
                  transition={{ duration: 0.6, ease: "easeOut" }}
                  style={{ position: 'relative', zIndex: 1 }}
                >
                  <label htmlFor="name" className="block mb-2 text-sm font-medium">
                    Name
                  </label>
                  <input
                    type="text"
                    id="name"
                    className="w-full p-3 rounded-lg border border-muted-200 bg-background focus:outline-none focus:ring-2 focus:ring-primary/50"
                    placeholder="Your name"
                    required
                  />
                </motion.div>
                <motion.div
                  initial={{ opacity: 0, x: 20 }}
                  whileInView={{ opacity: 1, x: 0 }}
                  viewport={{ once: true }}
                  transition={{ duration: 0.6, ease: "easeOut", delay: 0.1 }}
                  style={{ position: 'relative', zIndex: 1 }}
                >
                  <label htmlFor="email" className="block mb-2 text-sm font-medium">
                    Email
                  </label>
                  <input
                    type="email"
                    id="email"
                    className="w-full p-3 rounded-lg border border-muted-200 bg-background focus:outline-none focus:ring-2 focus:ring-primary/50"
                    placeholder="Your email"
                    required
                  />
                </motion.div>
              </div>
              
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ duration: 0.6, ease: "easeOut", delay: 0.2 }}
                style={{ position: 'relative', zIndex: 1 }}
              >
                <label htmlFor="subject" className="block mb-2 text-sm font-medium">
                  Subject
                </label>
                <input
                  type="text"
                  id="subject"
                  className="w-full p-3 rounded-lg border border-muted-200 bg-background focus:outline-none focus:ring-2 focus:ring-primary/50"
                  placeholder="How can we help you?"
                  required
                />
              </motion.div>
              
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ duration: 0.6, ease: "easeOut", delay: 0.3 }}
                style={{ position: 'relative', zIndex: 1 }}
              >
                <label htmlFor="message" className="block mb-2 text-sm font-medium">
                  Message
                </label>
                <textarea
                  id="message"
                  rows={5}
                  className="w-full p-3 rounded-lg border border-muted-200 bg-background focus:outline-none focus:ring-2 focus:ring-primary/50"
                  placeholder="Your message"
                  required
                />
              </motion.div>
              
              <motion.button
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ duration: 0.6, ease: "easeOut", delay: 0.4 }}
                whileHover={{ scale: 1.03 }}
                whileTap={{ scale: 0.98 }}
                type="submit"
                className="w-full btn-primary"
                style={{ position: 'relative', zIndex: 1 }}
              >
                Send Message
              </motion.button>
            </form>
          </motion.div>
        </motion.div>
      </div>
    </section>
  );
} 