"use client";

import { motion } from 'framer-motion';
import { 
  MessageSquare, 
  Bot, 
  FileText,
  Cpu
} from 'lucide-react';

export default function Features() {
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.2
      }
    }
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: {
      opacity: 1,
      y: 0,
      transition: { duration: 0.8, ease: "easeOut" }
    }
  };

  return (
    <section id="features" className="section bg-background">
      <div className="container">
        <motion.div 
          initial={{ opacity: 0, y: 50 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, amount: 0.3 }}
          transition={{ duration: 0.8, ease: "easeOut" }}
          className="stylish-box text-center mb-16"
          style={{ 
            position: 'relative',
            zIndex: 1 
          }}
        >
          <h2 className="text-3xl md:text-4xl font-bold mb-4">OUR SERVICES</h2>
          <p className="text-2xl font-bold mb-4">
            Sales, Customer Service, Marketing—Or All of the Above. You Choose.
          </p>
          <p className="text-muted-100 dark:text-muted-200 max-w-xl mx-auto">
            We build Conversational Agents, Content Automation and AI chatbots, so your business can work smarter, not harder
          </p>
        </motion.div>
        
        <motion.div 
          variants={containerVariants}
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.1 }}
          className="grid grid-cols-1 lg:grid-cols-1 gap-8"
          style={{ 
            position: 'relative',
            zIndex: 1 
          }}
        >
          <motion.div
            variants={itemVariants}
            className="service-card"
            style={{ 
              position: 'relative',
              zIndex: 1 
            }}
          >
            <div className="flex items-center mb-4">
              <MessageSquare className="w-8 h-8 text-primary mr-4" />
              <h3 className="text-2xl font-bold">AI-Powered Conversational Agents (NLP-Driven)</h3>
            </div>
            <p className="text-muted-100 dark:text-muted-200 text-lg">
              Provide round-the-clock customer engagement with advanced Natural Language Processing. Our Voice AI agents seamlessly handle FAQs, assist Customers in navigating products or services, and capture leads—delivering human-like interactions that boost customer satisfaction and retention.
            </p>
          </motion.div>

          <motion.div
            variants={itemVariants}
            className="service-card"
            style={{ 
              position: 'relative',
              zIndex: 1 
            }}
          >
            <div className="flex items-center mb-4">
              <FileText className="w-8 h-8 text-primary mr-4" />
              <h3 className="text-2xl font-bold">Generative AI Content Automation</h3>
            </div>
            <p className="text-muted-100 dark:text-muted-200 text-lg">
              Supercharge your content marketing with cutting-edge generative AI. Effortlessly create AI Avatars, social media updates, and campaigns—all while maintaining a consistent brand voice. Save time, enhance brand image, and maximize online visibility with minimal manual effort.
            </p>
          </motion.div>

          <motion.div
            variants={itemVariants}
            className="service-card"
            style={{ 
              position: 'relative',
              zIndex: 1 
            }}
          >
            <div className="flex items-center mb-4">
              <Bot className="w-8 h-8 text-primary mr-4" />
              <h3 className="text-2xl font-bold">AI-Powered Chatbots</h3>
            </div>
            <p className="text-muted-100 dark:text-muted-200 text-lg">
              Enhance customer engagement and automate lead qualification with intelligent, machine-learning-driven chatbots. Deliver instant responses, minimize wait times, and allow your team to focus on high-value tasks—boosting user experience and driving business growth.
            </p>
          </motion.div>
        </motion.div>
      </div>
    </section>
  );
} 