"use client";

import Link from 'next/link';
import { motion } from 'framer-motion';

export default function Hero() {
  return (
    <section id="hero" className="section bg-accent pt-20 pb-16 mt-[var(--nav-height)]">
      <div className="container pt-8">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 items-center">
          <motion.div
            initial={{ opacity: 0, y: 50 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8, ease: "easeOut" }}
            viewport={{ once: true, amount: 0.3 }}
            className="text-center lg:text-left"
            style={{ 
              position: 'relative',
              zIndex: 1 
            }}
          >
            <h1 className="text-4xl md:text-5xl lg:text-6xl font-bold mb-6 leading-tight">
              Smarter Systems, Stronger Businesses with <span className="text-primary">Cutting-Edge AI Solutions</span>
            </h1>
            <p className="text-lg mb-8 text-muted-100 dark:text-muted-200 max-w-xl mx-auto lg:mx-0">
              We make AI Agents easy and efficient, so you can grow your business hassle free - while focusing on what truly matters.
            </p>
            <div className="flex flex-col sm:flex-row gap-4 justify-center lg:justify-start">
              <Link href="/#contact" className="btn-primary text-center">
                Work With Us Today
              </Link>
              <Link href="/#features" className="btn-outline text-center">
                Explore Services
              </Link>
            </div>
          </motion.div>
          
          <motion.div
            initial={{ opacity: 0, scale: 0.9, y: 50 }}
            whileInView={{ opacity: 1, scale: 1, y: 0 }}
            transition={{ duration: 0.8, ease: "easeOut", delay: 0.2 }}
            viewport={{ once: true, amount: 0.3 }}
            className="flex justify-center"
            style={{ 
              position: 'relative',
              zIndex: 1 
            }}
          >
            <div className="relative w-full max-w-lg h-[400px] bg-gradient-to-br from-primary/20 to-secondary/20 rounded-2xl overflow-hidden">
              <div className="absolute inset-0 flex items-center justify-center">
                <div className="text-center">
                  <div className="w-24 h-24 bg-primary/80 backdrop-blur-lg rounded-full mx-auto flex items-center justify-center">
                    <svg 
                      xmlns="http://www.w3.org/2000/svg" 
                      viewBox="0 0 24 24" 
                      fill="none" 
                      stroke="currentColor" 
                      strokeWidth="2" 
                      strokeLinecap="round" 
                      strokeLinejoin="round" 
                      className="w-12 h-12 text-white"
                    >
                      <path d="M12 2a10 10 0 100 20 10 10 0 000-20z"></path>
                      <path d="M12 16v-4"></path>
                      <path d="M12 8h.01"></path>
                    </svg>
                  </div>
                  <h2 className="text-2xl font-bold mt-6 text-primary">FNXLabs</h2>
                  <p className="mt-2 text-muted-100 dark:text-muted-200">AI-Powered Solutions</p>
                </div>
              </div>
            </div>
          </motion.div>
        </div>
      </div>
    </section>
  );
} 